@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Poppins Regular"),
    local("Poppins-Regular"),
    url("../fonts/poppins-v15-latin-400.woff2") format("woff2"),
    url("../fonts/poppins-v15-latin-400.woff") format("woff");
}

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Poppins Medium"),
    local("Poppins-Medium"),
    url("../fonts/poppins-v15-latin-500.woff2") format("woff2"),
    url("../fonts/poppins-v15-latin-500.woff") format("woff");
}

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Poppins Bold"),
    local("Poppins-Bold"),
    url("../fonts/poppins-v15-latin-700.woff2") format("woff2"),
    url("../fonts/poppins-v15-latin-700.woff") format("woff");
}

@font-face {
  font-family: "Cormorant Garamond Italic";
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src: local("Cormorant Garamond Italic"),
    local("Cormorant-Garamond-Italic"),
    url("../fonts/cormorant-garamond-v9-latin-italic.woff2") format("woff2"),
    url("../fonts/cormorant-garamond-v9-latin-italic.woff") format("woff");
}

@font-face {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Cormorant Garamond Semibold"),
    local("Cormorant-Garamond-Semibold"),
    url("../fonts/cormorant-garamond-v9-latin-600.woff2") format("woff2"),
    url("../fonts/cormorant-garamond-v9-latin-600.woff") format("woff");
}

@font-face {
  font-family: "Cormorant Garamond";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Cormorant Garamond Bold"),
    local("Cormorant-Garamond-Bold"),
    url("../fonts/cormorant-garamond-v9-latin-700.woff2") format("woff2"),
    url("../fonts/cormorant-garamond-v9-latin-700.woff") format("woff");
}
